import Vue from "vue";
import App from "./App.vue";
import login from "./components/login.vue";
import home from "./components/home.vue"
import { defineCustomElements, applyPolyfills } from "@se/web-ui/loader";
import axios from 'axios';
import VueRouter from "vue-router";

Vue.use(VueRouter)
axios.defaults.baseURL = '/api/'  // api 即上面 vue.config.js 中配置的地址
axios.defaults.timeout = 300000 // 请求超时
Vue.prototype.$http = axios

// protect <se-*></se-*> component for being interpreted by VUE
Vue.config.ignoredElements = [/^se-/];

// Add custom element definition to the windows
// Bind the custom elements to the window object
applyPolyfills().then(() => {
  defineCustomElements(window);
});


Vue.config.productionTip = false;

const router = new VueRouter({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'root',
      redirect: '/login'
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/home',
      name: 'home',
      component: home
    }
  ]
});

new Vue({
  render: h => h(App),
  router: router
}).$mount("#app");
