<script>
  // import request from 'umi-request';
  export default {
    data() {
      return{
        appTitle: 'Smart PDU',
        version: '1.0.0',
        username: 'default',
        ip: '169.254.245.87',
        httpmode: 'http',
        loginStatus: 'logout',
        status: false,
        rows: {}
      }
    },
    
    created: function() {
      for (var i = 3; i < 8; i++){
        this.rows[i] = {
          "zone": "PTE0",
          "usage": "NotConfigured",
          "phSequence": "PhaseSeqABC",
          "mntPosition": "Up",
          "breakerRating": 1,
          "productType": "A9MEM1521",
          "slaveId": 3,
          "deviceAddress": 3791672315,
          "addressMode": "GPSourceId",
          "energyPActive": 567,
          "diagnosticEnable": true,
          "swVersion": "001.000.315",
          "serialNumber": "3N-2017-W41-5-T-0028",
          "hwVersion": "001.000.000",
          "epConfigUpdateStatus": 1,
          "epConfigUpdateSynthesis": 1,
          "energyPActiveRec": "N/A",
          "energyPReactive": "N/A",
          "energyPReactiveRec": "N/A",
          "powerFlow": "Mono",
          "loadRunningHourCounter": 0,
          "loadCounterTh": 10,
          "attrUpdateStatus": [
              134217728,
              2147693360,
              2290649224,
              143165568,
              8,
              0
          ],
          "nominalVoltage": 0,
          "DutyCycle": 5,
          "powerSupply": "UpStream",
          "DeviceNetworkState": "Networked",
          "currentA": 0,
          "currentB": "N/A",
          "currentC": "N/A",
          "voltageAB": "N/A",
          "voltageBC": "N/A",
          "voltageCA": "N/A",
          "voltageAN": 227.10,
          "voltageBN": "N/A",
          "voltageCN": "N/A",
          "powerA": 0,
          "powerB": "N/A",
          "powerC": "N/A",
          "powerTActive": 0,
          "powerTReactive": "N/A",
          "powerTApparent": "N/A",
          "powerFactorT": 1,
          "frequency": "N/A",
          "channel": 0,
          "neutralCurrent": "N/A",
          "powerPhAReactive": "N/A",
          "powerPhBReactive": "N/A",
          "powerPhCReactive": "N/A",
          "powerPhAApparent": "N/A",
          "powerPhBApparent": "N/A",
          "powerPhCApparent": "N/A",
          "powerFactorA": "N/A",
          "powerFactorB": "N/A",
          "powerFactorC": "N/A",
          "energyTActive": 567,
          "energyTReactive": "N/A",
          "energyTApparent": "N/A",
          "energyPApparent": "N/A",
          "energyTActiveRec": "N/A",
          "energyActDelMinusRecPHA": -9223372036854776000,
          "energyActDelMinusRecPHB": 0,
          "energyActDelMinusRecPHC": 0,
          "loadResetTime": "2000-01-01T00:00:53+00:00",
          "groundFaultLevel": 65535
        }
      }
      console.log(this.rows);
    },

    mounted: function() {
      this.username = this.$route.params.username;
      this.getData();
    },


    methods: {
      
      changeStatus: function($event,num){
        // console.log(this.$refs.myCheckBox);
        console.log($event.detail.selected);
        this.$http.post('/rsa1/Controller/methods/operateMio',
          {"slaveId":2,
            "channel":1,
            "status":$event.detail.selected?'ON':'OFF'}).then((data)=>{
                console.log(data);
        });
        if ($event.detail.selected){
          setTimeout(() =>  {
            this.turn_off();
          }, 500);
          
        }
        
      },
      turn_on(){
        this.$http.post('/rsa1/Controller/methods/operateMio',
          {"slaveId":2,
            "channel":1,
            "status":'ON'}).then((data)=>{
                console.log(data);
        });
        
      },
      turn_off(){
        this.$http.post('/rsa1/Controller/methods/operateMio',
          {"slaveId":2,
            "channel":1,
            "status":'OFF'}).then((data)=>{
                console.log(data);
                
                this.$refs.myCheckBox.selected = false;
        });
      },
      login(){
        this.$http.post('/rs/login',
          {username: this.username, password: this.password}).then((data)=>{
                console.log(data);
        });
      },
      logout(){
        
        this.$router.push('login');
      },
      getData(){
        this.$http.get('/rsa1/WirelessMeter/instances').then((data)=>{
          // console.log(data.data);
          for (var i = 0, len = data.data.length; i < len; i++){
            this.rows[data.data[i].slaveId] = data.data[i];
          }
          // console.log(this.rows);
        });
        this.$http.get('/rsa1/MeterCumulatedData').then((data)=>{
          // console.log(data.data);
          for (var i = 0, len = data.data.MeterCumulatedData.length; i < len; i++){
            var slaveId = data.data.MeterCumulatedData[i].slaveId;
            Object.assign(this.rows[slaveId], data.data.MeterCumulatedData[i]);
          }
        });
        this.$http.get('/rsa1/MeterInstantData').then((data)=>{
          // console.log(data.data.MeterInstantData);
          for (var i = 0, len = data.data.MeterInstantData.length; i < len; i++){
            var slaveId = data.data.MeterInstantData[i].slaveId;
            Object.assign(this.rows[slaveId], data.data.MeterInstantData[i]);
            for (var key in this.rows[slaveId]){
              if (typeof(this.rows[slaveId][key]) == 'number'){
                this.rows[slaveId][key] = (Math.round(this.rows[slaveId][key]*1000)/1000).toFixed(3);
              }
              if (this.rows[slaveId][key] == 18446744073709551615 ||
                  this.rows[slaveId][key] == 4290772990){
                    this.rows[slaveId][key] = 'N/A';
                  }
            }
          }
          console.log(this.rows);
          this.$forceUpdate();
        });
      }
    }
  };
</script>

