<template>
  <se-authentication id="se-authentication" app-title="Test Advisor" version="demo" image-url="url(https://www.nozominetworks.com/wp-content/uploads/2019/01/Nozomi-Networks-and-Schneider-Electric-Partner-Secure-Industrial-Infrastructure.jpg)">
    <se-form-field block label="Username" required="true" option="stacked">
      <input type="text"
             v-model="username"/>
      </se-form-field>
      <se-form-field block label="Password" required="true" option="stacked">
        <input type="password" 
               v-model="password"/>
    </se-form-field>
    <se-button v-on:click="login()" option="login" value="myLoginValue">Login</se-button>
    <se-snackbar ref="snackbar" type="error" icon="information_stroke"></se-snackbar>
  </se-authentication>
</template>
<script>
  // import request from 'umi-request';
  export default {
    data() {
      return{
        username:'',
        password:''
      }
    },
    methods: {
      login(){
        console.log(this.username);
        this.$http.post('/rs/login', {
          username: this.username, password: this.password}).then(response=>{
          this.$router.push({
            name: 'home',
            params: {
              username: this.username
            }
          });
        }).catch((error) => {
          console.log(error);
          this.$refs.snackbar.message = error.message;
          this.$refs.snackbar.open = true;
          this.$refs.snackbar.canClose = true;
        });
        
        // this.$router.push({
        //   name: 'home',
        //   params: {
        //     username: this.username
        //   }
        // });
      },
    }
  };
</script>

